@charset "utf-8";

//button
.btn-small {
    min-height: 30px;
    padding: 0 10px;
    border: 0;
    color: #fff;
    vertical-align: top;
    
    @include fontSize(16px);
    @include border-radius(2px);
    @include box-sizing;

    & + .btn-medium {
        margin-left: 6px;
    }
}
a.btn-small {
    display: inline-block;
    text-align: center;
    line-height: 30px;

    &:hover {
        color: #fff;
    }
}

.btn-medium {
    min-height: 40px;
    padding: 0 15px;
    border: 0;
    color: #fff;
    vertical-align: top;
    
    @include fontSize(16px);
    @include border-radius(2px);
    @include box-sizing;

    & + .btn-medium {
        margin-left: 6px;
    }
}
a.btn-medium {
    display: inline-block;
    text-align: center;
    line-height: 40px;

    &:hover {
        color: #fff;
    }
}

.btn-large {
    min-width: 100px;
    min-height: 50px;
    padding: 0 30px;
    border: 0;
    color: #fff;
    vertical-align: top;
    
    @include fontSize(16px);
    @include border-radius(2px);
    @include box-sizing;

    & + .btn-large {
        margin-left: 6px;
    }
}
a.btn-large {
    display: inline-block;
    text-align: center;
    line-height: 50px;

    &:hover {
        color: #fff;
    }
}

.btn-color1 {
    background-color: #0b52bc;
}
.btn-color2 {
    background-color: #888;
}
.btn-color3 {
    background-color: #666;
}

.btn-dn {
    position: relative;
    padding-left: 22px;
    border: 0;
    color: #333;
    background: none;

    @include fontSize(14px);

    &:before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        width: 17px;
        height: 23px;
        margin-top: -11px;
    }

    &.btn-dn1 {
        &:before {
            background: url(../images/comm/sprite.png) no-repeat -194px 0;
        }
    }

    &.btn-dn2 {
        &:before {
            background: url(../images/comm/sprite.png) no-repeat -221px 0;
        }
    }

    &.btn-dn3 {
        &:before {
            background: url(../images/comm/sprite.png) no-repeat -263px -59px;
        }
    }
}
a.btn-dn {
    display: inline-block;
    vertical-align: top;
    line-height: 1.7;
}

// form
.input-text {
    height: 40px;
    padding: 0 15px;
    border: 1px solid #bbb;
    color: #333;
    background-color: #fff;

    @include fontSize(16px);
    @include border-radius(2px);
    @include box-sizing;
}

.form-radio {
    display: inline-block;
    min-height: 20px;
    position: relative;
    vertical-align: middle;

    .form-radio-label {
        display: inline-block;
        position: relative;
        padding-left: 30px;
        color: #333;
        vertical-align: top;
        line-height: 1.2;

        @include fontSize(16px);

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            background: url(../images/comm/sprite.png) no-repeat -203px -59px;

            @include size(20px);
            @include box-sizing;
            @include border-radius(2px);
        }

        &:after {
            content: "";
            display: none;
            position: absolute;
            left: 5px;
            top: 5px;
            background-color: #2262c1;

            @include size(10px);
            @include border-radius(50px);
        }
    }

    .form-radio-input {
        overflow: hidden;
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        font-size: 1%;
        z-index: -9999;
        clip: rect(1px, 1px, 1px, 1px);
        clip-path: inset(50%);
        opacity: 0;
        z-index: -9999;

        &:checked + .form-radio-label {
            &:after {
                display: block;
            }
        }
    }

    &.hide {
        .form-radio-label {
            padding-left: 0;

            @include size(20px);
        }
    }
}

.form-checkbox {
    display: inline-block;
    position: relative;
    min-height: 20px;
    vertical-align: middle;
    line-height: 1.3;

    .form-checkbox-label {
        display: inline-block;
        position: relative;
        padding-left: 30px;
        color: #333;
        vertical-align: top;
        line-height: 1.2;

        @include fontSize(16px);

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            border: 1px solid #999;
            background-color: #fff;

            @include size(20px);
            @include box-sizing;
            @include border-radius(2px);
        }

        &:after {
            content: "";
            display: none;
            position: absolute;
            left: 0;
            top: 0;
            background: url(../images/comm/sprite.png) no-repeat -233px -59px;

            @include size(20px);
        }
    }

    .form-checkbox-input {
        overflow: hidden;
        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        font-size: 1%;
        z-index: -9999;
        clip: rect(1px, 1px, 1px, 1px);
        clip-path: inset(50%);
        opacity: 0;
        z-index: -9999;

        &:checked + .form-checkbox-label {
            &:after {
                display: block;
            }
        }
    }
}

.form-select {
    min-width: 120px;
    height: 40px;
    padding: 0 8px;
    border: 1px solid #bbb;
    color: #333;

    @include box-sizing;
    @include fontSize(16px);
    @include border-radius(2px);
}

.form-msg {
    margin-left: 20px;
    color: #f15118;

    @include fontSize(14px);
}

.form-group {
    font-size: 0;

    [class^=btn-],
    .input-text,
    .form-select {
        vertical-align: middle;
    }

    .input-text + [class^=btn-],
    .input-text + .form-select {
        margin-left: 10px;
    }

    .form-select {
        & + .form-select {
            margin-left: 10px;
            vertical-align: middle;
        }
    }

    .form-radio,
    .form-checkbox {
        margin-right: 40px;

        &:last-child {
            margin-right: 0;
        }
    }

    .dash,
    .at {
        display: inline-block;
        padding: 0 8px;
        color: #333;
        vertical-align: middle;
        text-align: center;

        @include fontSize(16px);
    }

    &.align-ctr {
        text-align: center;
    }

    .form-msg {
        display: inline-block;
        vertical-align: middle;
    }
}

// 검색
.search-board {
    padding: 20px 70px;
    border: 1px solid #ced7dc;
    font-size: 0;
    background-color: #f9fdff;

    .search-board-in {
        position: relative;
        padding-right: 120px;
        white-space: nowrap;

        @include clear;
    }

    .search-board-item {
        float: left;
        margin-right: 20px;

        @include flexbox;
        @include align-items(center);

        label,
        .tit {
            margin-right: 20px;
            color: #333;
            
            @include fontSize(18px);
        }

        .select-search {
            margin-right: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    .select-search {
        min-width: 120px;
        height: 40px;
        margin-right: 10px;
        padding: 0 8px;
        border: 1px solid #bbb;
        vertical-align: middle;

        @include border-radius(2px);
        @include fontSize(16px);
        @include box-sizing;
    }

    .input-search {
        width: calc(100% - 130px);
        height: 40px;
        padding: 0 15px;
        border: 1px solid #bbb;
        vertical-align: middle;

        @include border-radius(2px);
        @include fontSize(16px);
        @include box-sizing;
    }

    .form-checkbox,
    .form-radio {
        margin-top: 10px;
        margin-right: 20px;
        margin-bottom: 10px;

        .form-checkbox-label,
        .form-radio-label {
            margin: 0;

            @include fontSize(16px);
        }
    }

    .btn-search {
        position: absolute;
        right: 0;
        top: 0;
        width: 90px;
        height: 40px;
        border: 0;
        color: #fff;
        background-color: #606b7f;

        @include fontSize(16px);
        @include border-radius(2px);
    }
}

