.board-input {
    width: 100%;
    border: 1px solid #d4d4d4;
    background-color: #fff;

    @include box-sizing;

    th {
        padding: 22px 30px;
        border-bottom: 1px solid #d4d4d4;
        color: #333;
        font-weight: 500;
        background-color: #f6f6f6;
        text-align: left;

        @include fontSize(16px);
    }

    td {
        padding: 10px 20px;
        border-bottom: 1px solid #d4d4d4;
    }

    tr {
        &:last-child {
            th,
            td {
                border-bottom: 0;
            }
        }
    }
}

.board {
    width: 100%;
    border: 1px solid #d4d4d4;
    background-color: #fff;

    @include box-sizing;

    th {
        height: 41px;
        padding: 9px 10px;
        color: #111;
        font-weight: 500;
        background-color: #f6f6f6;

        @include fontSize(16px);
    }

    th,
    td {
        border: 1px solid #d4d4d4;

        &.align-lft {
            text-align: left;
        }
    }

    td {
        padding: 9px 10px;
        color: #333;
        text-align: center;

        @include fontSize(16px);
    }

    .btn-learn {
        min-width: 84px;
        min-height: 30px;
        padding: 2px 10px;
        border: 0;
        color: #fff;

        @include fontSize(16px);
        @include border-radius(2px);
        @include box-sizing;
        @include transition(background .2s);

        &:hover {
            background-color: #0094e0;
        }
    }
    a.btn-learn {
        display: inline-block;
        text-align: center;
        line-height: 1.65;
        vertical-align: top;
    }
}

.board-list {
    width: 100%;
    border-top: 2px solid #606b7f;

    th {
        height: 50px;
        border-bottom: 1px solid #d4d4d4;
        color: #666;
        font-weight: 400;
        background-color: #f6f6f6;

        @include fontSize(16px);
    }

    td {
        height: 39px;
        padding: 10px;
        border-bottom: 1px solid #d4d4d4;
        background-color: #fff;
        text-align: center;

        &.align-lft {
            text-align: left;
        }
    }

    .highlight {
        color: #f15118;
    }
}

.board-view {
    width: 100%;
    border-top: 2px solid #606b7f;

    th {
        height: 49px;
        color: #333;
        font-weight: 500;
        background-color: #f6f6f6;

        @include fontSize(16px);
    }

    td {
        padding: 10px 20px;
        color: #333;
        background-color: #fff;

        @include fontSize(16px);
    }

    th,
    td {
        border-bottom: 1px solid #d4d4d4;
    }

    .attach-file {
        color: #2262c1;
    }

    .contents {
        padding: 30px 40px;
        line-height: normal;

        p {
            margin: unset;
            padding: unset;
        }

        img {
            max-width: 100%;
        }
    }
}

// paging
.paging {
    text-align: center;

    .btn-paging {
        display: inline-block;
        margin-left: 5px;
        color: #222;
        vertical-align: middle;
        text-align: center;
        line-height: 40px;

        @include size(40px);
        @include fontSize(16px);
        @include box-sizing;
        @include border-radius(2px);

        &:first-child {
            margin-left: 5px;
        }

        &.is-active {
            color: #fff;
           background-color: #606b7f; 
        }
    }

    .btn-paging-quick {
        display: inline-block;
        position: relative;
        border: 1px solid #d7d7d7;
        background-color: #fff;
        vertical-align: middle;

        @include size(40px);
        @include fontSize(16px);
        @include box-sizing;
        @include border-radius(2px);

        &:before {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            background-image: url(../images/comm/sprite.png);
            background-repeat: no-repeat;

            @include translate(-50%, -50%);
        }

        &.btn-paging-first {
            margin-right: 5px;

            &:before {
                width: 17px;
                height: 16px;
                background-position: -151px -33px;
            }
        }

        &.btn-paging-prev {
            margin-right: 15px;

            &:before {
                width: 9px;
                height: 16px;
                background-position: -178px -33px;
            }
        }

        &.btn-paging-next {
            margin-left: 15px;

            &:before {
                width: 9px;
                height: 16px;
                background-position: -197px -33px;
            }
        }

        &.btn-paging-last {
            margin-left: 5px;

            &:before {
                width: 17px;
                height: 16px;
                background-position: -216px -33px;
            }
        }
    }
}

// 문의
.help-guide {
    position: relative;
    max-width: 800px;
    min-height: 100px;
    margin: 0 auto;
    padding: 31px 60px;
    border: 1px solid #e2e2e2;
    background-color: #fff;

    @include flexbox;
    @include align-items(center);
    @include border-radius(5px);
    @include box-sizing;

    .help-guide-tel {
        position: relative;
        padding-left: 67px;
        font-size: 0;

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 53px;
            height: 46px;
            margin-top: -23px;
            background: url(../images/comm/sprite.png) no-repeat 0 -18px;
        }
    }

    .tit {
        display: block;
        margin-bottom: 10px;
        font-weight: 500;

        @include fontSize(20px);

        
    }

    .tel {
        display: block;
        font-family: "Roboto", "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
        color: #f15118;

        @include fontSize(24px);

        & + .tel {
            margin-top: 5px;
        }
    }

    ul {
        position: absolute;
        top: 50%;
        right: 60px;
        letter-spacing: 0;

        @include translate(0, -50%);
    }

    li {
        color: #333;
        line-height: 1.2;

        @include fontSize(16px);

        span {
            color: #666;
        }
    }
}

// 서브페이지 상단
.sub-header {
    position: relative;
    height: 240px;
    padding-bottom: 100px;
    background-repeat: repeat;
    background-position: 50% 0;
    text-align: center;

    @include flexbox;
    @include justify-content(flex-end);
    @include align-items(center);
    @include flex-direction(column);
    @include box-sizing;

    .title {
        color: #fff;
        font-weight: 500;
        @include fontSize(36px);
    }

    .location {
        position: absolute;
        width: 100%;
        max-width: 1200px;
        bottom: 0;
        left: 50%;
        background: rgba(0, 0, 0, .5);

        @include translate(-50%, 0);
        @include flexbox;

        .location-home {
            position: relative;
            width: 71px;
            height: 60px;
            border-right: 1px solid rgba(0, 0, 0, .2);

            &:before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                width: 25px;
                height: 22px;
                margin: -11px 0 0 -12px;
                background: url(../images/comm/sprite.png) no-repeat -76px -74px;
            }
        }

        .location-item {
            position: relative;
            height: 60px;
            width: calc((220/1200) * 100%);
            padding: 0 15px;
            border-right: 1px solid rgba(0, 0, 0, .2);
            color: #ccc;
            font-weight: 350;
                        
            @include flexbox;
            @include align-items(center);
            @include fontSize(20px);
            @include box-sizing;

            &:after {
                content: "";
                position: absolute;
                top: 50%;
                right: 15px;
                width: 9px;
                height: 16px;
                margin-top: -8px;
                background: url(../images/comm/sprite.png) no-repeat -111px -93px;
                opacity: .5;
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }

            &.is-active {
                color: #fff;
                background: rgba(0, 0, 0, .2);

                &:after {
                    opacity: 1;
                }
            }
        }
    }
}

// lnb
.lnb {
    overflow: hidden;
    // position: absolute;
    // top: 0;
    // left: 0;
    float: left;
    width: 160px;
    margin-right: 40px;

    .title {
        display: block;
        height: 60px;
        color: #fff;
        font-weight: 500;
        background: #2262c1;
        text-align: center;
        line-height: 60px;
        opacity: .9;

        @include fontSize(22px);
        @include border-radius(8px 8px 0 0);
    }

    .lnb-wrap {
        overflow: hidden;
        border-width: 0 1px 1px;        
        border-style: solid;
        border-color: #bbb;
        background-color: #fff;
        
        @include border-radius(0 0 8px 8px);
    }

    .lnb-sub {
        > li {
            border-top: 1px solid #bbb;

            &:first-child {
                border-top: 0;
            }

            &:last-child {
                .lnb-sub-sub {
                    padding-bottom: 9px;
                }
            }

            &.is-on {
                // .lnb-sub-sub {
                //     display: block;
                // }

                .btn-lnb-sub {
                    &:after {
                        width: 14px;
                        height: 2px;
                        margin-top: -1px;
                        background-position-x: -248px;
                    }
                }
                a.btn-lnb-sub {
                    color: #f15118;
                }
            }
        }
    }

    .btn-lnb-sub {
        position: relative;
        width: 100%;
        min-height: 50px;;
        padding: 0 20px;
        border: 0;
        color: #111;
        font-weight: 500;
        background-color: #f6f6f6;
        text-align: left;

        @include fontSize(20px);
        @include box-sizing;

        &:after {
            content: "";
            position: absolute;
            right: 20px;
            top: 50%;
            margin-top: -7px;
            background: url(../images/comm/sprite.png) no-repeat -36px 0;

            @include size(14px);
        }
    }
    a.btn-lnb-sub {
        display: inline-block;
        padding: 15px 20px;
        vertical-align: top;

        &:after {
            display: none;
        }
    }

    .lnb-sub-sub {
        display: none;
        padding: 15px 20px 0;

        > li {
            margin-top: 15px;
            padding-bottom: 16px;
            background: url(../images/comm/dot_lnb.gif) repeat-x 0 100%;

            .is-active {
                color: #f15118;
            }

            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                background: none;
            }
        }

        .tit {
            color: #333;
            font-weight: 500;

            @include fontSize(18px);
        }

        ul {
            margin-top: 15px;

             li {
                 margin-top: 15px;
                color: #333;

                @include fontSize(16px);

                &:first-child {
                    margin-top: 0;
                }
             }
        }
    }
}

// tab
.tab {
    overflow: hidden;
    background-color: #f6f6f6;

    @include flexbox;

    .btn-tab {
        height: 60px;
        margin-right: -1px;
        padding: 10px;
        border: 1px solid #d4d4d4;
        color: #333;
        text-align: center;

        @include flex;
        @include flexbox;
        @include justify-content(center);
        @include align-items(center);
        @include box-sizing;
        @include fontSize(22px);

        &:first-child {
            @include border-radius(5px 0 0 5px);
        }

        &:last-child {
            margin-right: 0;

            @include border-radius(0 5px 5px 0);
        }

        &.is-active {
            position: relative;
            border-color: #bbb;
            color: #111;
            font-weight: 500;
            background-color: #fff;

            @include fontSize(24px);
        }
    }
}

// 학교검색 레이어
.pop-layer {
    position: absolute;
    // top: 50%;
    left: 50%; 
    border: 1px solid #4b515b;
    background-color: #fff;
    z-index: 9;

    @include box-sizing;
    @include box-shadow(0 9px 40px 0 rgba(0, 0, 0, 0.24));
    @include translate(-50%, 0);

    .pop-head {
        height: 50px;
        padding: 0 20px;
        color: #fff;
        background-color: #4b515b;
        line-height: 50px;

        @include fontSize(20px);
    }

    .pop-search {
        border: 1px solid #ced7dc;
        background-color: #f9fdff;

        .pop-search-in {
            padding: 10px 20px;
            
            @include flexbox;
            @include align-items(center);
        }

        .label {
            margin-right: 20px;
            color: #333;
            white-space: nowrap;

            @include fontSize(18px);
        }

        .btn-search {
            margin-left: 5px;
            color: #fff;
            background-color: #606b7f;
        }
    }

    .btn-pop-clse {
        position: absolute;
        top: 6px;
        right: 10px;
        border: 0;
        background: url(../images/comm/btn_pop_clse.png) no-repeat 50% 50%;

        @include size(38px);
    }

    // 학교검색
    &.pop-school {
        max-width: 500px;

        .pop-body {
            padding: 20px 30px;
        }

        .school-list {
            margin-top: 20px;
            padding-bottom: 50px;

            .tit {
                display: block;
                margin-bottom: 10px;
                color: #333;

                @include fontSize(16px);
            }

            .board-wrap {
                overflow-y: auto;
                max-height: 299px;
            }

            .board-list {
                th {
                    height: 41px;
                }

                td {
                    height: 29px;
                }

                .search-word {
                    color: #1d77dd;
                }

                .btn-school {
                    width: 60px;
                    height: 30px;
                    border: 0;
                    color: #fff;

                    @include border-radius(2px);
                    @include fontSize(16px);
                }
            }
        }
    }
}
  