@charset "utf-8";

/* 웹폰트 */
@include webFont(Noto Sans KR, notosanskr, notosanskr100, 100);
@include webFont(Noto Sans KR, notosanskr, notosanskr300, 300);
@include webFont(Noto Sans KR, notosanskr, notosanskr350, 350);
@include webFont(Noto Sans KR, notosanskr, notosanskrregular, 400);
@include webFont(Noto Sans KR, notosanskr, notosanskr500, 500);
@include webFont(Noto Sans KR, notosanskr, notosanskr700, 700);
@include webFont(Noto Sans KR, notosanskr, notosanskr900, 900);
@include webFont(Roboto, roboto, roboto100, 100);
@include webFont(Roboto, roboto, roboto, 400);
@include webFont(Roboto, roboto, roboto500, 500);

// basic
html {
    scroll-behavior: smooth;
}

body {
    font-family: "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
    font-size: 16px;
    font-weight: 400;
    background-color: #e1e1e1;
    line-height: 1;
    letter-spacing: -1px;

    @include wordwrap;
}

// common class
.ir {
    overflow: hidden;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    font-size: 1%;
    z-index: -9999;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
}

.is-hidden {
    display: none;
}

#skip {
    a {
        position: absolute;
        left: 0;
        top: -30px;
    }
}

// layout
#wrap {
    overflow: hidden;
    min-width: 320px;
}

// header
.header {
    position: relative;
    background: #fff;
    z-index: 9;

    .header-wrap {
        position: relative;
        max-width: 1200px;
        height: 90px;
        margin: 0 auto;
        padding: 27px 0;

        @include box-sizing;
    }

    .ci {
        img {
            vertical-align: top;
        }
    }

    .header-menu {
        position: absolute;
        right: 0;
        bottom: 25px;

        @include flexbox;
        @include align-items(center);

        .header-menu-list {
            @include clear;

            li {
                position: relative;
                float: left;
                margin-left: 16px;
                padding-left: 18px;

                &:before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 0;
                    margin-top: -2px;
                    background-color: #8a8a8a;

                    @include size(4px);
                }

                &:first-child {
                    margin-left: 0;
                    padding-left: 0;

                    &:before {
                        display: none;
                    }
                }
            }

            .btn-menu {
                color: #333;

                @include fontSize(16px);
            }
        }

        .header-menu-font {
            margin-left: 30px;

            @include flexbox;
            @include align-items(center);

            em {
                margin-right: 10px;
                color: #666;
                font-weight: 350;

                @include fontSize(14px);
                // font-weight: 300;
            }

            .btn-font {
                position: relative;
                border: 1px solid #e2e2e2;
                background: #fff;

                @include size(14px);
                @include box-sizing;
                @include border-radius(2px);

                & + .btn-font {
                    margin-left: 5px;
                }
            }

            .btn-up {
                &:before {
                    content: "";
                    display: inline-block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    margin: -4px 0 0 -4px;
                    background: url(../images/comm/sprite.png) no-repeat 0 0;
                    
                    @include size(8px);
                }
            }

            .btn-down {
                &:before {
                    content: "";
                    display: inline-block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: 8px;
                    height: 2px;
                    margin: -1px 0 0 -4px;
                    background: url(../images/comm/sprite.png) no-repeat -18px 0;
                }
            }
        }
    }

    .header-menu-mobile {
        display: none;
        position: absolute;
        top:50%;
        right: 10px;

        @include align-items(center);
        @include translate(0, -50%);

        .btn-menu-login {
            display: block;
            position: relative;
            margin-right: 25px;
            padding-left: 20px;
            color: #333;
            font-weight: 500;
            line-height: 1.3;

            @include fontSize(16px);

            &:before {
                content: "";
                display: inline-block;
                position: absolute;
                left: 0;
                top: 50%;
                width: 14px;
                height: 18px;
                margin-top: -9px;
                background: url(../images/comm/ico_human.png) no-repeat 50% 50%;
                background-size: contain;
            }
        }

        .btn-menu-mobile {
            border: 0;
            background: url(../images/comm/ico_hamburger.png) no-repeat 50% 50%;
            background-size: 25px auto;

            @include size(36px);
        }
    }

    .btn-gnb-clse {
        display: none;
        position: absolute;
        right: 5px;
        top: 9px;
        border: 0;
        background: url(../images/comm/btn_gnb_clse.png) no-repeat 50% 50%;
        background-size: 20px 20px;

        @include size(40px);
    }
}

//gnb
.gnb {
    position: absolute;
    top: 90px;
    left: 0;
    width: 100%;
    background-color: rgba(11, 82, 188, .9);

    @include box-shadow(0 2px 3px 0 rgba(0, 0, 0, .2));

    .gnb-wrap {
        max-width: 1200px;
        margin: 0 auto;

        @include flexbox;
        @include justify-content(space-between);
    }

    .btn-gnb {
        width: 100%;
        height: 60px;
        border: 0;
        color: #cceeff;
        white-space: nowrap;
        background: transparent;

        @include fontSize(24px);
        @include transition(color .2s);
    }

    .gnb-sub {
        // display: none;
        position: absolute;
        top: 55px;
        left: 50%;
        min-width: 160px;
        padding: 2px 10px;
        border: 2px solid #1b5bbb;
        background-color: #f6f6f6;
        opacity: 0;
        z-index: -9999;

        @include border-radius(5px);
        @include box-sizing;
        @include box-shadow(0 4px 10px 0 rgba(0, 0, 0, 0.5));
        @include translate(-50%, 0);
        @include transition(opacity .25s, top .3s);

        li {
            border-bottom: 1px solid #d4d4d4;

            &:last-child {
                border-bottom: 0;
            }
        }

        .btn-gnb-sub {
            display: block;
            padding: 9px 10px 10px;
            color: #111;
            font-weight: 500;
            white-space: nowrap;

            @include fontSize(18px);
            @include transition(color .2s);

            &:hover {
                color: #f15118;
            }

            &.is-active {
                color: #f15118;
            }
        }
    } 
    
    .gnb-box {
        overflow: hidden;
        position: relative;
        padding: 0 60px;
        background-color: transparent;

        &.is-active,
        &:hover,
        &:focus {
            overflow: visible;

            .btn-gnb {
                color: #fff;
            }

            .gnb-sub {
                // display: block;
                top: 48px;
                opacity: 1;
                z-index: auto;
            }
        }
    }
}

.gnb-mobile {
    display: none;
    overflow-y: auto;
    position: absolute;
    top: 58px;
    left: 0;
    width: 100%;
    max-height: calc(100vh - 58px);
    background-color: #fff;

    @include scrollTouch;

    .gnb-box {
        margin-top: 1px;

        &:first-child {
            margin-top: 0;
        }
    }

    .btn-gnb {
        position: relative;
        width: 100%;
        height: 55px;
        padding: 0 15px;
        border: 0;
        color: #cceeff;
        font-weight: 500;
        background-color: #2363c3;
        text-align: left;

        @include fontSize(20px);
        @include box-sizing;

        &:after {
            content: "";
            position: absolute;
            right: 15px;
            top: 50%;
            margin-top: -10px;
            background: url(../images/comm/ico_plus_large.png) no-repeat 0 0;
            background-size: contain;

            @include size(20px);
        }
    }

    .gnb-sub {
        display: none;

        > li {
            border-bottom: 1px solid #bbb;

            &:last-child {
                border-bottom: 0;
            }
        }
    }

    .btn-gnb-sub {
        position: relative;
        width: 100%;
        height: 50px;
        padding: 0 15px;
        border: 0;
        color: #111;
        font-weight: 500;
        background-color: #f6f6f6;
        text-align: left;

        @include fontSize(20px);
        @include box-sizing;

        &:after {
            content: "";
            position: absolute;
            right: 17px;
            top: 50%;
            margin-top: -8px;
            background: url(../images/comm/ico_plus_medium.png) no-repeat 0 0;
            background-size: contain;

            @include size(16px);
        }
    }
    a.btn-gnb-sub {
        display: inline-block;
        vertical-align: top;
        line-height: 50px;

        &:after {
            display: none;
        }
    }

    .gnb-sub-sub {
        display: none;
        padding: 0 30px;
        
        > li {
            padding: 12px 0;
            background: url(../images/comm/dot_gnb_mobile.gif) repeat-x 0 0;
            background-size: 4px auto;

            .tit {
                display: block;
                padding: 3px 0 12px;
                color: #111;
                font-weight: 500;

                @include fontSize(18px);
            }

            ul {
                li {
                    padding: 7px 0;
                    color: #666;

                    @include fontSize(16px);
                }
            }

            &:first-child {
                background: none;
            }
        }
    }
}

// footer
.footer {
    border-top: 1px solid #d4d4d4;

    .footer-menu {
        border-bottom: 1px solid #d4d4d4;
        background-color: #fff;     
        
        .footer-menu-list {
            height: 59px;

            @include flexbox;
            @include justify-content(center);
            @include align-items(center);

            li {
                position: relative;
                margin-left: 20px;
                padding-left: 24px;

                &:before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 0;
                    margin-top: -2px;
                    background-color: #8a8a8a;

                    @include size(4px);
                }

                &:first-child {
                    margin-left: 0;
                    padding-left: 0;

                    &:before {
                        display: none;
                    }
                }
            }

            .btn-footer-menu {
                color: #1b1b1b;
                font-weight: 500;

                &.color {
                    color: #0d5694;
                }
            }
        }   
    }

    .footer-info {
        padding: 30px 0 60px;

        .footer-address {
            @include flexbox;
            @include justify-content(center);

            li {
                margin-left: 18px;

                &:first-child {
                    margin-left: 0;
                }
            }

            address {
                display: inline;
                color: #666;
                font-weight: 350;
            }

            .email {
                color: #666;
            }

            & + .footer-address {
                margin-top: 8px;
            }
        }

        .footer-agency {
            margin-top: 25px;

            @include flexbox;
            @include justify-content(center);
            @include align-items(center);

            li {
                margin-left: 18px;
                
                &:first-child {
                    margin-left: 0;
                };
            }

            img {
                vertical-align: top;
            }
        }

        .copyright {
            margin-top: 20px;
            color: #666;
            font-weight: 350;
            text-align: center;
            letter-spacing: 0;
        }
    }
}

// icon
.ico-required {
    color: #f15118;
    font-weight: 500;

    @include fontSize(16px);
}

.section-wrap {
    max-width: 1200px;
    margin: 0 auto;
}

// 로그인
.section-login {
    padding-bottom: 40px;
    background-color: #f2f2f2;

    .sub-header {
        margin-bottom: 100px;
        padding-bottom: 74px;
        background-image: url(../images/bg_join.jpg);
    }

    .login-wrap {
        max-width: 798px;
        margin: 0 auto;
        padding: 58px 0 75px;
        border: 1px solid #d7d7d7;
        background-color: #fff;
        text-align: center;

        .title {
            color: #111;
            font-weight: 500;
    
            @include fontSize(24px);
    
            &:before {
                content: "";
                display: block;
                margin: 0 auto 10px;
                background: url(../images/comm/sprite.png) no-repeat 0 -90px;
    
                @include size(62px);
            }
        }
    }

    .login-box {
        max-width: 280px;
        margin: 40px auto 0;
    }

    .login-input {
        margin-bottom: 30px;
    }

    .login-input-box {
        display: block;

        .input-text {
            width: 100%;
            text-align: center;
        }

        & + .login-input-box {
            margin-top: 10px;
        }
    }

    .btn-login {
        width: 100%;
    }

    .help-guide {
        margin-top: 160px;
    }
}

.coming-soon {
    padding: 50px 0;
    font-weight: 500;
    text-align: center;

    @include fontSize(24px);
}

// 이용약관
.section-terms {
    padding-bottom: 40px;
    background-color: #f2f2f2;

    .sub-header {
        margin-bottom: 60px;
        padding-bottom: 74px;
        background-image: url(../images/bg_join.jpg);
    }

    .terms-conts {
        h3 {
            margin-bottom: 35px;
            color: #111;
            font-weight: 500;

            @include fontSize(16px);
        }

        h4 {
            margin-bottom: 10px;
            color: #333;
            font-weight: 500;

            @include fontSize(16px);
        }

        p,
        > ol,
        > ul {
            margin-bottom: 25px;
            color: #666;
            line-height: 1.4;

            @include fontSize(14px);
        }

        p {
            & + ol {
                margin-top: -15px;
            }
        }

        ol,
        ul {
            ol,
            ul {
                margin: 5px 0;
                padding: 0 15px;
            }
        }

        .board-list {
            margin: 10px 0;
            table-layout: fixed;

            th {
                text-align: center;
            }

            td {
                height: auto;
            }
        }

        .terms-conts-noti {
            display: block;
            margin-bottom: 35px;
            color: #111;
            font-weight: 500;

            @include fontSize(16px);
        }
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}

// 사이트맵
.section-sitemap {
    padding-bottom: 40px;
    background-color: #f2f2f2;

    .sub-header {
        margin-bottom: 60px;
        padding-bottom: 74px;
        background-image: url(../images/bg_join.jpg);
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}