@charset "utf-8";

// max width 1024
@include respond-to(ipad_pro) {
    // header
    .header {
        .header-wrap {
            height: 58px;
            padding: 20px 15px;
        }

        .ci {
            img {
                height: 18px;
            }
        }

        .header-menu {
            display: none;
        }

        .header-menu-mobile {
            @include flexbox;
        }
    }

    // gnb
    .gnb {
        display: none;
    }

    .sub-header {
        height: 120px;
        padding-bottom: 65px;
        background-size: cover;

        .title {
            @include fontSize(30px);
        }

        .location {
            right: 15px;
            left: 15px;
            width: auto;

            @include translate(0, 0);

            .location-home,
            .location-item {
                display: none;
                height: 40px;
            }

            .location-item {
                width: 50%;

                &.is-active,
                &.is-active ~ .location-item {
                    @include flexbox;
                }
            }
        }
    }

    // lnb
    .lnb {
        display: none;
    }

    // 테이블
    .board-wrap {
        .board-scroll {
            overflow-y: hidden;
            overflow-x: auto;

            @include scrollTouch;
        }

        .form-msg {
            display: block;
            margin: 10px 0 0 0;
        }
    }

    // 문의
    .help-guide {
        min-height: 80px;
    }

    // tab
    .tab {
        .btn-tab {
            height: 45px;

            @include fontSize(16px);

            &.is-active {
                @include fontSize(18px);
            }
        }
    }

    .section-wrap {
        margin-right: 15px;
        margin-left: 15px;
    }

    // 로그인
    .section-login {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 50px;
            padding-bottom: 37px;
        }

        .help-guide {
            margin-top: 80px;
        }
    }

    // 회원가입
    .section-join {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 50px;
            padding-bottom: 37px;
        }

        .join-box {
            .title {
                margin-bottom: 10px;

                @include fontSize(20px);
            }

            .join-info-required {
                top: 5px;
            }
        }

        .help-guide {
            margin-top: 80px;
        }
    }

    // index
    .section-index {
        padding-bottom: 55px;

        .index-top {
            .index-swiper {
                .swiper-wrapper {
                    height: 320px;
                }

                .swiper-util {
                    bottom: 110px;
                    padding: 0 122px; 

                    .swiper-util-wrap {
                        padding: 0;
                    }

                    .swiper-pagination {
                        .swiper-pagination-current {
                            @include fontSize(12px);
                        }

                        .swiper-pagination-total {
                            @include fontSize(10px);

                            &:before {
                                margin-top: -4px;
                                height: 7px;
                            }
                        }
                    }                    

                    .btn-swiper-prev,
                    .btn-swiper-next {
                        display: none;
                    }
                }
            }
        }

        .index-main {
            margin-top: 35px;
            padding: 0 15px;

            .index-aside {
                .index-help {
                    // padding: 0 5%;

                    .tit {
                        @include fontSize(18px);
                    }

                    .tel {
                        @include fontSize(16px);
                    }

                    ul {
                        margin-left: 20px;
                        white-space: nowrap;

                        @include fontSize(15px);
                    }
                }
            }
        }
    }

    // 회원정보 변경
    .section-mypage {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 30px;
            padding-bottom: 37px;
        }

        h3.title {
            margin-bottom: 10px;

            @include fontSize(20px);
        }

        .help-guide {
            margin-top: 100px;
        }
    }

    // 교수학습자료
    .section-study {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 30px;
        }

        .study-wrap {
            padding: 0;
        }

        .prev-data {
            .prev-data-box {
                margin-top: 30px;
                padding-top: 0;
            }
            
            .prev-data-top {
                li {
                    padding: 12px 130px 12px 15px;
                }

                span {
                    @include fontSize(15px);
                }

                .btn-learning-map {
                    right: 15px;
                    min-width: 81px;
                    min-height: 25px;

                    @include fontSize(14px);
                }
                a.btn-learning-map {
                    line-height: 1.5;
                }
            }
        }

        .sub-contents {
            float: none;
            width: auto;
            
            h3.title {
                margin-bottom: 10px;

                @include fontSize(20px);
            }
        }

        .learning-data {
            padding-top: 0;
        }

        .help-guide {
            margin-top: 100px;
        }
    }

    // 커뮤니티
    .section-community {
        .sub-contents {
            float: none;
            width: auto;
        }
    }

    // 학생정보관리
    .section-student {
        .sub-contents {
            float: none;
            width: auto;
        }
    }

    // 학교관리
    .section-school {
        .sub-header {
            margin-bottom: 30px;
            // padding-bottom: 37px;
        }

        h3.title {
            margin-bottom: 10px;

            @include fontSize(20px);
        }

        .sub-contents {
            float: none;
            width: auto;
        }

        .teacher-wrap {
            padding-top: 0;

            h3.title {
                margin-bottom: 10px;

                @include fontSize(20px);
            }
        }

        .help-guide {
            margin-top: 100px;
        }
    }

    // 소개
    .section-intro {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 30px;
            // padding-bottom: 37px;
        }

        .intro-wrap {
            padding: 0;
        }

        .sub-contents {
            float: none;
            width: auto;
        }

        // hijump 소개
        .hijump-intro {
            padding-top: 0;

            h3.title {
                margin-bottom: 10px;
    
                @include fontSize(20px);
            }

            .hijump-intro-conts {
                padding: 13px 20px;
            }
            
            .hijump-intro-flow {
                ol {
                    li {
                        .subject {
                            br {
                                display: inline;
                            }
                        }
                    }
                }
            }
        }

        .notice-wrap {
            padding-top: 0;

            h3.title {
                margin-bottom: 10px;

                @include fontSize(20px);
            }
        }

        .qna-wrap {
            padding-top: 0;

            h3.title {
                margin-bottom: 10px;

                @include fontSize(20px);
            }
        }

        .help-guide {
            margin-top: 100px;
        }
    }

    // 이용약관
    .section-terms {
        padding-bottom: 20px;

        .sub-header {
            margin-bottom: 30px;
            padding-bottom: 37px;
        }

        .help-guide {
            margin-top: 100px;
        }
    }

    // 가로모드
    @include respond-to(landscape) {

    }
}

// max width 768
@include respond-to(ipad) {
    // footer
    .footer {
        .footer-info {
            padding-right: 15px;
            padding-left: 15px;

            .footer-address {
                @include flex-direction(column);
                @include align-items(center);

                li {
                    margin: 9px 0 0 0;

                    &:first-child {
                        margin-top: 0;
                    }
                }

                & + .footer-address {
                    margin-top: 9px;
                }
            }

            .footer-agency {
                padding: 0 15px;

                img {
                    max-width: 100%;
                }
            }
        }
    }

    .search-board {
        padding: 15px;
    }

    .board-list {
        colgroup {
            display: none;
        }

        th,
        td {
            padding: 10px;
        }
    }

    .board-view {
        .contents {
            padding: 20px 30px;
        }
    }

    // 로그인
    .section-login {
        .login-wrap {
            margin-right: 15px;
            margin-left: 15px;
        }
    }
    
    // index
    .section-index {
        .index-top {
            .index-swiper {
                .swiper-wrapper {
                    height: 240px;
                }

                .swiper-util {
                    bottom: 80px;
                    padding: 0 92px; 
                }
            }
        }

        .index-main {
            .index-notice,
            .index-aside {
                float: none;
                width: auto;
            }

            .index-aside {
                margin-top: 30px;                
                
                .index-help {
                    margin-top: 30px;
                    // padding: 0 5%;

                    .tit {
                        @include fontSize(22px);
                    }

                    .tel {
                        @include fontSize(20px);
                    }

                    ul {
                        margin-left: 70px;

                        @include fontSize(18px);
                    }
                }
            }
        }
    }

    // 교수학습자료
    .section-study {
        .learning-data {
            .learning-data-head {
                padding: 15px;
            }

            .learning-data-head-info {
                .thumb {
                    float: none;
                    width: auto;
                    margin: 0;
                    text-align: center;
                }

                .info {
                    margin-top: 20px;
                    padding-top: 0;

                    li {
                        margin-top: 10px;
                    }
                }
            }
        }
    }

    // 소개 
    .section-intro {
        .qna-wrap {
            .board-input {
                th {
                    text-align: left;
                }
            }
        }

        .hijump-intro {
            .hijump-intro-flow {
                .hijump-intro-box {
                    padding: 15px;
                    
                    @include flex-direction(column);
                    @include align-items(flex-start);
                }

                .hijump-intro-service-item {
                    width: auto;
                    margin-top: 20px;

                    li {
                        // white-space: normal;
                    }
                }
            }
        }
    }

    .help-guide {
        margin-right: 15px;
        margin-left: 15px;
        padding: 15px 20px;

        .tit {
            margin-right: 0;

            @include fontSize(15px);
        }
        
        .tel {
            display: block;
            margin-top: 8px;

            @include fontSize(20px);
        }

        li {
            span {
                display: block;
            }
        }
    }
}

// max- width 568
@media only screen and (max-width: 568px) {
    .help-guide {
        ul {
            right: 20px;
        }

        li {
            @include fontSize(14px);
        }
    }

    .board-input {
        colgroup {
            display: none;
        }

        th {
            display: block;
            padding: 10px;
        }

        td {
            display: block;
            padding: 10px;
        }
    }

    // 검색
    .search-board {
        .search-board-in {
            padding: 0;
        }
        
        .search-board-item {
            float: none;
            margin: 0;

            .select-search {
                @include flex;
            }

            .form-group {
                white-space: normal;
            }

            // .form-checkbox {
            //     margin-top: 0;
            //     margin-bottom: 0;
            // }

            & + .search-board-item {
                margin-top: 10px;
            }
        }

        .btn-search {
            position: static;
            display: block;
            margin: 10px auto 0;
        }
    }

    // 회원가입
    .section-join {
        .join-step {
            li {
                display: none;
                margin: 0;

                &:after {
                    display: none;
                }

                &.is-active {
                    display: block;
                }
            }
        }
    }

    // index
    .section-index {
        .index-top {
            .index-swiper {
                .swiper-wrapper {
                    height: 216px;
                }

                .swiper-util {
                    bottom: 70px;
                    padding: 0 15px; 
                }
            }
        }

        .index-main {
            .index-aside {
                .index-help {
                    height: auto;
                    padding: 15px 20px;

                    .tit {
                        @include fontSize(15px);
                    }

                    .tel {
                        @include fontSize(20px);
                    }

                    ul {
                        margin-left: 20px;

                        @include fontSize(14px);
                    }
                }
            }
        }
    }

    // 학교관리
    .section-school {
        .teacher-wrap {
            .teacher-button {
                @include flex-direction(column);

                .teacher-button-rgt {
                    margin-top: 10px;
                }
            }
        }
    }

    // 소개
    .section-intro {
        // hijump 소개
        .hijump-intro {
            .hijump-intro-conts {
                padding: 13px 20px;

                > p {
                    & + p {
                        margin-top: 26px;
                    }
                }
            }

            .hijump-intro-flow {
                .hijump-intro-service-item {
                    li {
                        white-space: normal;
                    }
                }
            }
        }

        // 공지사항
        .notice-wrap {
            .board-input {
                th {
                    text-align: left;
                }
            }
        }
    }

    .help-guide {
        .help-guide-tel {
            width: 60%;

            @include box-sizing;
        }
       
        .tel {
            @include fontSize(16px);
        }
    }

    // pop
    .pop-layer {
        left: 15px;
        right: 15px;

        @include translate(0, 0);

        .pop-search {
            .pop-search-in {
                // @include flex-direction(column);
                // @include align-items(flex-start);

                .label {
                    margin-right: 10px;
                    // margin-bottom: 10px;
                }

                .input-text {
                    width: 100%;
                    // max-width: none !important;
                    // @include flex;
                }
            }
        }
    }
}

// max width 414
@include respond-to(mobiles) {
    // footer
    .footer {
        .footer-menu {
            .footer-menu-list {
                height: 49px;

                li {
                    margin-left: 14px;
                    padding-left: 16px;

                    &:before {
                        margin-top: -1px;

                        @include size(2px);
                    }
                }
                
                .btn-footer-menu {
                    @include fontSize(14px);
                }
            }
        }

        .footer-info {
            padding: 20px 0 40px;

            .footer-address {
                @include fontSize(12px);
                @include flex-direction(column);
                @include align-items(center);

                li {
                    margin: 9px 0 0 0;

                    &:first-child {
                        margin-top: 0;
                    }
                }

                & + .footer-address {
                    margin-top: 9px;
                }
            }

            .footer-agency {
                li {
                    margin-left: 12px;;
                }

                .moe {
                    width: 70px;
                }

                .kice {
                    width: 109px;
                }

                .teenup {
                    width: 112px;
                }

                .hifive {
                    width: 91px;
                }
            }

            .copyright {
                margin-top: 20px;
                
                @include fontSize(12px);
            }
        }
    }

    // index
    .section-index {
        .index-top {
            .index-swiper {
                .swiper-wrapper {
                    height: auto;

                    .item {
                        position: static;

                        @include translate(0, 0);
                    }

                    .img {
                        display: none;

                        &.is-mobile {
                            display: inline-block;
                            max-width: 100%;
                            max-height: none;
                        }
                    }
                }

                .swiper-util {
                    bottom: 58%;
                }
            }
        }

        .index-main {
            .index-notice {
                height: auto;
                padding: 20px 15px;

                .index-notice-list {
                    margin-top: 15px;

                    li {
                        margin-top: 12px;

                        @include fontSize(14px);
                    }
                }

                .btn-more {
                    top: 26px;
                    right: 15px;
                    padding-right: 16px;

                    @include fontSize(12px);

                    &:after {
                        margin-top: -5px;
                        background: url(../images/comm/ico_plus.png) no-repeat 0 0;
                        background-size: cover;

                        @include size(11px);
                    }
                }
            }

            .index-aside {
                .index-guide {
                    .index-guide-guide,
                    .index-guide-manual {
                        height: 90px;
                    }

                    .index-guide-guide {
                        a:after {
                            width: 40px;
                            height: 45px;
                            margin-left: 20px;
                            background-size: contain;
                        }
                    }

                    .index-guide-manual {
                        a:after {
                            margin-left: 15px;
                            background-size: contain;

                            @include size(45px);
                        }
                    }
                }

                .index-help {
                    .index-help-tel {
                        padding-left: 53px;
                        
                        &:before {
                            width: 45px;
                            height: 40px;
                            margin-top: -20px;
                            background-size: contain;
                        }
                    }

                    // .tit {
                    //     @include fontSize(15px);
                    // }

                    .tel {
                        @include fontSize(16px);
                    }

                    // ul {
                    //     margin-left: 15px;
                    //     letter-spacing: 0;

                    //     @include fontSize(14px);
                    // }
                }
            }
        }
    }

    // 소개 
    .section-intro {
        .hijump-intro {
            .hijump-intro-flow {
                .hijump-intro-service-item {
                    // @include flex-wrap(wrap);

                    li {
                        margin-right: 4px;
                        padding: 5px;
                        white-space: normal;

                        br {
                            display: none;
                        }
                        
                        // @include flex(none);
                    }
                }
            }
        }
    }
}