@charset "utf-8";

.section-school {
    padding-bottom: 40px;
    background: #f2f2f2;

    .sub-header {
        margin-bottom: 40px;
        background-image: url(../images/bg_intro.jpg);
    }

    .school-wrap {
        // position: relative;
        // padding-left: 200px;

        @include clear;
    }

    .sub-contents {
        float: left;
        width: calc(100% - 200px);
    }

    .school-edit {
        position: relative;
    }

    h3.title {
        margin-bottom: 20px;
        color: #111;
        font-weight: 500;

        @include fontSize(24px);
    }

    .school-edit-required {
        position: absolute;
        top: 10px;
        right: 0;
        color: #666;
        font-weight: 350;

        @include fontSize(14px);

        .ico-required {
            @include fontSize(14px);
        }
    }

    .school-edit-button {
        margin-top: 30px;
        text-align: center;
    }

    .teacher-wrap {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .search-board {
            margin-bottom: 30px;
        }

        .teacher-button {
            margin-top: 10px;

            @include flexbox;
        }

        .teacher-noti {
            margin-top: 4px;
            margin-right: auto;
            color: #666;

            @include fontSize(14px);

            strong {
                color: #0b52bc;
                font-weight: 400;
            }
        }

        .teacher-button-rgt {
            margin-left: auto;
            white-space: nowrap;
        }

        .paging {
            margin-top: 10px;
        }
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}