@charset "utf-8";

.section-mypage {
    padding-bottom: 40px;
    background-color: #f2f2f2;

    .sub-header {
        margin-bottom: 60px;
        background-image: url(../images/bg_join.jpg);
    }

    .mypage-edit {
        position: relative;
    }

    h3.title {
        margin-bottom: 20px;
        color: #111;
        font-weight: 500;

        @include fontSize(24px);
    }

    .mypage-edit-required {
        position: absolute;
        top: 10px;
        right: 0;
        color: #666;
        font-weight: 350;

        @include fontSize(14px);

        .ico-required {
            @include fontSize(14px);
        }
    }

    .mypage-edit-button {
        margin-top: 30px;
        text-align: center;
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}