@charset "utf-8";

.section-index {
    padding-bottom: 70px;
    background-color: #f2f2f2;

    .index-top {
        position: relative;
        
        .index-swiper {
            .swiper-wrapper {
                height: 490px;
            }

            .swiper-slide {
                overflow: hidden;
            }

            .item {
                display: block;
                position: absolute;
                top: 0;
                left: 50%;
                height: 100%;
                text-align: center;

                @include translate(-50%, 0);

                .img {
                    max-height: 100%;

                    &.is-mobile {
                        display: none;
                    }
                }
            }

            .swiper-util {
                position: absolute;
                bottom: 164px;
                left: 50%;
                width: 100%;
                max-width: 1200px;
                z-index: 1;

                @include translate(-50%, 0);
                @include box-sizing;
                @include clear;

                .swiper-util-wrap {
                    position: relative;
                    float: left;
                    padding: 0 25px;
                }

                .swiper-pagination {
                    position: static;
                    font-family: "Roboto", "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
                    color: #666;
                    text-align: left;
                    font-size: 0;

                    .swiper-pagination-current {
                        color: #333;

                        @include fontSize(20px);
                    }

                    .swiper-pagination-total {
                        position: relative;
                        margin-left: 15px;
                        font-weight: 300;

                        @include fontSize(18px);

                        &:before {
                            content: "";                        
                            position: absolute;
                            top: 50%;
                            left: -8px;
                            width: 1px;
                            height: 10px;
                            margin-top: -5px;
                            background-color: #b0b8bf;
                        }
                    }
                }

                .btn-swiper-prev,
                .btn-swiper-next {
                    width: 20px;
                    height: 18px;
                    margin-top: -9px;
                    border: 0;
                    background: none;

                    &:before {
                        content: "";
                        display: inline-block;
                        width: 12px;
                        height: 18px;
                    }

                    &:after {
                        display: none;
                    }
                }

                .btn-swiper-prev {
                    left: 0;

                    &:before {
                        background: url(../images/comm/sprite.png) no-repeat -150px 0;
                    }
                }

                .btn-swiper-next {
                    right: 0;

                    &:before {
                        background: url(../images/comm/sprite.png) no-repeat -172px 0;
                    }
                }
            }
        }
    }

    .index-main {
        max-width: 1200px;
        margin: 60px auto 0;

        @include clear;

        .index-notice {
            position: relative;
            float: left;
            width: calc((540 / 1200) * 100%);
            height: 272px;
            padding: 20px;
            border: 1px solid #ccc;
            background-color: #fff;

            @include box-sizing;
            @include border-radius(2px);

            .title {
                padding-bottom: 12px;
                border-bottom: 1px solid #e2e2e2;
                color: #333;
                font-weight: 500;

                @include fontSize(20px);
            }

            .index-notice-list {
                margin-top: 20px;

                li {
                    margin-top: 20px;
                    color: #333;
                    font-weight: 350;

                    @include fontSize(18px);
                    @include truncate;

                    &:first-child {
                        margin-top: 0;
                    }
                }
            }

            .btn-more {
                display: inline-block;
                position: absolute;
                top: 22px;
                right: 20px;
                padding-right: 23px;
                color: #333;
                font-weight: 500;

                @include fontSize(16px);

                &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    right: 1px;
                    margin-top: -7px;
                    background: url(../images/comm/sprite.png) no-repeat -36px 0;

                    @include size(14px);
                }
            }
        }

        .index-aside {
            float: right;
            width: calc((600 / 1200) * 100%);

            .index-guide {
                @include clear;

                .index-guide-guide,
                .index-guide-manual {
                    float: left;
                    width: calc((100% - 30px) / 2);
                    height: 120px;
                    border: 1px solid #ccc;
                    color: #000;
                    font-weight: 500;
                    background-color: #fff;

                    @include box-sizing;
                    @include fontSize(20px);
                    @include border-radius(2px);
                    @include transition(border-color .25s, box-shadow .25s);

                    a {
                        display: block;
                        height: 100%;
                        color: #000;

                        @include flexbox;
                        @include justify-content(center);
                        @include align-items(center);
                    }

                    &:hover {
                        border-color: #91b1df;

                        @include box-shadow(0 7px 15px 0 rgba(0, 0, 0, 0.08));
                    }
                }

                .index-guide-guide {
                    a {
                        &:after {
                            content: "";
                            // position: absolute;
                            display: block;
                            width: 58px;
                            height: 65px;
                            margin-left: 19%;
                            background: url(../images/comm/ico_guidebook.png) no-repeat 0 0;
                        }
                    }
                }
                  

                .index-guide-manual {
                    float: right;
                    line-height: 1.1;

                    a {
                        &:after {
                            content: "";
                            // position: absolute;
                            display: block;
                            width: 71px;
                            height: 72px;
                            margin-left: 15.5%;
                            background: url(../images/comm/ico_keyboard.png) no-repeat 0 0;
                        }
                    }
                }
            }

            .index-help {
                height: 112px;
                padding: 0 6%;
                margin-top: 40px;
                border: 1px solid #e2e2e2;
                background: #fff;
                
                @include flexbox;
                @include align-items(center);
                @include box-sizing;
                @include border-radius(2px);

                .index-help-tel {
                    position: relative;
                    padding-left: 80px;

                    &:before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 0;
                        width: 61px;
                        height: 54px;
                        margin-top: -27px;
                        background: url(../images/comm/ico_tel.png) no-repeat 0 0;
                    }
                }

                .tit {
                    display: block;
                    margin-bottom: 8px;
                    color: #000;
                    font-weight: 500;

                    @include fontSize(20px);
                }

                .tel {
                    display: block;
                    font-family: "Roboto", "Noto Sans KR", "맑은고딕", "Malgun Gothic", sans-serif;
                    color: #f15118;
                    letter-spacing: 0;

                    @include fontSize(18px);

                    & + .tel {
                        margin-top: 5px;
                    }
                }

                ul {
                    margin-left: 35px;
                    color: #333;
                    line-height: 1.2;
                    letter-spacing: 0;

                    @include fontSize(16px);

                    span {
                        display: block;
                        color: #666;
                    }
                }
            }
        }
    }
}