@charset "utf-8";

.section-join {
    padding-bottom: 40px;
    background-color: #f2f2f2;

    .sub-header {
        margin-bottom: 60px;
        padding-bottom: 74px;
        background-image: url(../images/bg_join.jpg);
    }

    .join-step {
        @include flexbox;
        @include justify-content(center);

        li {
            position: relative;
            margin-left: 80px;
            padding: 14px 36px 16px;
            border: 2px solid #ccc;
            color: #999;
            background-color: #fff;
            text-align:  center;

            @include border-radius(300px);

            strong {
                display: block;
                margin-top: 8px;
                color: #666;
                font-weight: 400;

                @include fontSize(24px);
            }

            &:after {
                content: "";
                position: absolute;
                top: 50%;
                right: -75px;
                width: 66px;
                height: 6px;
                margin-top: -3px;
                background: url(../images/comm/sprite.png) no-repeat 0 -74px;
            }

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                &:after {
                    display: none;
                }
            }

            &.is-active {
                border-color: #666;
            }
        }

        .title-step {
            color: #333;
            font-weight: 400;

            @include fontSize(16px);

            strong {
                color: #f15118;
                font-weight: 500;
            }
        }
    }

    .join-title {
        margin-top: 38px;
        color: #1d77dd;
        font-weight: 400;
        text-align: center;

        @include fontSize(20px);
    }

    .join-dsc {
        margin-top: 14px;
        color: #666;
        text-align: center;
    }

    .join-box {
        position: relative;
        margin-top: 29px;

        .title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .join-terms-conts {
            overflow-y: auto;
            height: 538px;
            padding: 30px;
            border: 1px solid #d7d7d7;
            background-color: #fff;

            @include box-sizing;
            @include scrollTouch;

            h4 {
                margin-bottom: 35px;
                color: #111;
                font-weight: 500;

                @include fontSize(16px);
            }

            h5 {
                margin-bottom: 10px;
                color: #333;
                font-weight: 500;

                @include fontSize(16px);
            }

            p,
            > ol {
                margin-bottom: 25px;
                color: #666;
                line-height: 1.4;

                @include fontSize(14px);
            }

            p {
                & + ol {
                    margin-top: -15px;
                }
            }

            ol {
                ol {
                    margin: 5px 0;
                    padding: 0 15px;
                }
            }
        }

        .join-terms-check {
            margin-top: 15px;
        }

        .join-terms-button {
            margin-top: 20px;
            text-align: center;
        }

        .join-info-required {
            position: absolute;
            top: 10px;
            right: 0;
            color: #666;
            font-weight: 350;
    
            @include fontSize(14px);
    
            .ico-required {
                @include fontSize(14px);
            }
        }

        .join-board-button {
            margin-top: 35px;
            text-align: center;
        }
        
        .join-step-success {
            padding: 75px 20px 100px;
            border: 1px solid #d7d7d7;
            background-color: #fff;
            text-align: center;

            .join-step-greeting {
                margin-bottom: 25px;

                &:before {
                    content: "";
                    display: block;
                    width: 64px;
                    height: 60px;
                    margin: 0 auto;
                    background: url(../images/comm/sprite.png) no-repeat -76px 0;
                }
    
                span {
                    display: block;
                    margin-top: 25px;
                    color: #666;
                    line-height: 1.5;
    
                    @include fontSize(16px);
    
                    em {
                        color: #0b52bc;
                    }
    
                    strong {
                        display: block;
                        margin-top: 21px;
                        color: #111;
                        font-weight: 500;
    
                        @include fontSize(20px);
                    }
                }
            }

            .btn-large {
                min-width: 120px;
            }
        }
    }

    .help-guide {
        margin-top: 160px;
    }
}