@charset "utf-8";

.section-study {
    padding-bottom: 40px;
    background: #f2f2f2;

    .sub-header {
        margin-bottom: 40px;
        background-image: url(../images/bg_join.jpg);
    }

    .study-wrap {
        // position: relative;
        // padding-left: 200px;

        @include clear;
    }

    .sub-contents {
        float: left;
        width: calc(100% - 200px);

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }
    }

    .learning-data {
        padding-top: 15px;
        
        .learning-data-box {
            margin-top: 40px;

            &:first-child {
                margin-top: 0;
            }
        }

        .learning-data-head {
            padding: 30px 40px;
            border: 1px solid #ced7dc;
            background-color: #f9fdff;
        }

        .learning-data-head-info {
            @include clear;

            .thumb {
                float: left;
                width: 210px;
                margin-right: 30px;
                padding: 5px;
                border: 1px solid #d4d4d4;
                background-color: #fff;

                @include border-radius(5px);
                @include box-sizing;

                img {
                    max-width: 100%;
                    vertical-align: top;
                }
            }      

            .info {
                overflow: hidden;
                padding-top: 20px;

                > ul {
                    > li {
                        margin-top: 20px;
                        line-height: 1.2;

                        &:first-child {
                            margin-top: 0;
                        }
                    }

                    .tit {
                        display: inline-block;
                        min-width: 100px;
                        margin-right: 20px;
                        padding: 10px 15px;
                        color: #fefefe;
                        font-weight: 400;
                        background-color: #606b7f;
                        text-align: center;
                        vertical-align: top;

                        @include fontSize(16px);
                        @include border-radius(100px);
                        @include box-sizing;
                    }

                    .conts {
                        display: block;
                        margin-top: 10px;
                        padding: 0 15px;
                        color: #333;

                        @include fontSize(16px);
                    }

                    ul {
                        margin-top: 7px;
                        padding-left: 15px;
                        color: #333;

                        @include fontSize(16px);

                        li {
                            position: relative;
                            margin-top: 4px;
                            padding-left: 15px;

                            &:before {
                                content: "";
                                position: absolute;
                                top: 7px;
                                left: 0;
                                background-color: #adb5c4;

                                @include size(5px);
                                @include border-radius(50px);
                            }

                            &:first-child {
                                margin-top: 0;
                            }
                        }
                    }
                }

                .info-learningmap {
                    margin-top: 10px;
                    text-align: right;
                }
            }
        }

        // .learning-data-head-noti {
        //     margin-top: 20px;
        //     padding: 20px 0 0 10px;
        //     color: #666;
        //     background: url(../images/comm/dot_lnb.gif) repeat-x 0 0;

        //     @include fontSize(16px);

        //     li {
        //         margin-top: 12px;
                
        //         &::first-letter {
        //             margin-left: -18px;
        //             padding-left: 10px;
        //         }

        //         &:first-child {
        //             margin-top: 0;
        //         }
        //     }
        // }

        .learning-data-files {
            border-top: 2px solid #606b7f;
        }

        .learning-data-files-top {
            position: relative;
            padding: 19px 130px 19px 30px;
            border-bottom: 1px solid #d4d4d4;
            color: #333;
            background-color: #fffefa;
            line-height: 1.2;

            @include fontSize(18px);

            .btn-dn {
                position: absolute;
                top: 50%;
                right: 30px;

                @include translate(0, -50%);
            }
        }

        .learning-data-files-list {
            background-color: #fff;

            li {
                border-bottom: 1px solid #d4d4d4;
            }

            dl {
                padding: 0 30px;
            }

            dt {
                position: relative;
                padding: 17px 100px 17px 0;
                color: #333;
                line-height: 1.2;

                @include fontSize(16px);

                .btn-dn {
                    position: absolute;
                    right: 0;
                    top: 50%;

                    @include translate(0, -50%);
                }
            }

            dd {
                position: relative;
                padding: 17px 60px 17px 88px;
                color: #666;
                background: url(../images/comm/dot_lnb.gif) repeat-x 0 0;
                line-height: 1.3;

                @include fontSize(16px);
                @include box-sizing;

                .label {
                    display: block;
                    position: absolute;
                    top: 17px;
                    left: 0;
                    min-width: 70px;
                    margin-right: 10px;
                    padding: 4px;
                    color: #fff;
                    background-color: #606b7f;
                    text-align: center;
                    line-height: 1;

                    @include fontSize(14px);
                    @include border-radius(100px);
                }

                .conts {
                    overflow: hidden;
                    max-height: 20px;

                    @include transition(max-height ease .5s);

                    &.is-on {
                        max-height: 100px;
                    }
                }

                .btn-slide {
                    position: absolute;
                    top: 16px;
                    right: 0;
                    width: 49px;
                    height: 22px;
                    border: 1px solid #e2e2e2;
                    background-color: #fbfbfb;

                    @include border-radius(2px);

                    &:before {
                        content: "";
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: 14px;
                        height: 7px;

                        @include translate(-50%, -50%);
                    }

                    &.btn-slide-down {
                        &:before {
                            background: url(../images/comm/sprite.png) no-repeat -272px 0;
                        }
                    }

                    &.btn-slide-up {
                        &:before {
                            background: url(../images/comm/sprite.png) no-repeat -272px -17px;
                        }
                    }
                }
            }
        }
    }

    .prev-data {
        .prev-data-box {
            margin-top: 50px;
            padding-top: 15px;

            &:first-child {
                margin-top: 0;
            }
        }

        .prev-data-top {
            border: 1px solid #ced7dc;
            background-color: #fff;
            
            li {
                position: relative;
                padding: 20px 30px;
                border-top: 1px solid #ced7dc;
                
                &:first-child {
                    border-top: 0;
                }
            }

            span {
                color: #333;

                @include fontSize(18px);
            }

            .btn-learning-map {
                position: absolute;
                right: 13%;
                top: 50%;
                min-width: 102px;
                min-height: 30px;
                padding: 2px 10px;
                border: 0;
                color: #fff;
                background-color: #299d0e;

                @include fontSize(16px);
                @include box-sizing;
                @include border-radius(2px);
                @include translate(0, -50%);
             }
             a.btn-learning-map {
                 display: inline-block;
                 vertical-align: top;
                 line-height: 1.65;
                 text-align: center;
                 letter-spacing: 0;
             }
        }

        .tab-contents {
            margin-top: 30px;

            .search-board {
                margin-bottom: 30px;
            }

            .tab-box {
                & + .tab-box {
                    margin-top: 40px;
                }
            }
        }

        .prev-data-button {
            margin-top: 10px;
            text-align: right;
        }

        .paging {
            margin-top: 10px;
        }
    }

    .learning-map {
        position: relative;
        padding-top: 15px;

        .tab {
            margin-bottom: 30px;
        }

        .btn-map-list {
            position: absolute;
            top: 20px;
            right: 0;
            color: #666;
            text-decoration: underline;

            @include fontSize(16px);
        }

        .learning-map-top {
            text-align: right;

            .btn-list-view {
                width: 113px;
                height: 32px;
                border: 1px solid #e2e2e2;
                color: #333;
                background-color: #fbfbfb;

                @include border-radius(3px);
                @include fontSize(14px);
                @include box-sizing;

                span {
                    position: relative;
                    display: inline-block;
                    padding-right: 19px;

                    &:after {
                        content: "";
                        display: inline-block;
                        position: absolute;
                        top: 50%;
                        right: 0;
                        width: 14px;
                        height: 7px;
                        margin-top: -3px;
                        background: url(../images/comm/sprite.png) no-repeat -272px 0;
                    }
                }

                &.is-active {
                    span {
                        &:after {
                            background-position-y: -17px;
                        }
                    }                
                }
            }
        }

        .learning-map-list {
            margin-top: 10px;
            border-bottom: 1px solid #d4d4d4;
            background-color: #fff;

            .learning-map-item {
                .subject {
                    // height: 39px;
                    padding: 16px 30px;
                    border-top: 2px solid #ced7dc;
                    border-bottom: 1px solid #d4d4d4;
                    color: #0b52bc;
                    font-weight: 400;
                    background-color: #f9fdff;
                    line-height: 1.3;
                    cursor: pointer;

                    @include fontSize(18px);
                    @include flexbox;
                    @include align-items(center);
                }

                .conts {
                    display: none;

                    .subject-dsc {
                        // height: 33px;
                        padding: 16px 30px;
                        border-bottom: 1px solid #d4d4d4;
                        color: #333;
                        font-weight: 400;
                        background-color: #fffefa;
                        line-height: 1.3;
    
                        @include fontSize(16px);
                        @include flexbox;
                        @include align-items(center);
                    }

                    ul {
                        padding: 0 30px;

                        & + .subject-dsc {
                            border-top: 1px solid #d4d4d4;
                        }
                    }

                    li {
                        padding: 13px 20px;
                        background: url(../images/comm/dot_lnb.gif) repeat-x 0 100%;
                        line-height: 1.3;

                        @include flexbox;
                        @include align-items(center);

                        &:last-child {
                            background: none;
                        }
                    }

                    .btn-view {
                        display: inline-block;
                        margin-left: 5px;
                        color: #fff;
                        background-color: #0b52bc;
                        text-align: center;
                        line-height: 29px;
                        vertical-align: middle;

                        @include flex(none);
                        @include size(29px);
                        @include fontSize(16px);
                        @include border-radius(100px);

                        &:first-child {
                            margin-left: 10px;
                        }
                    }
                }
            }
        }
    }

    .help-guide {
        margin-top: 190px;
    }
}