@charset "utf-8";

.section-intro {
    padding-bottom: 40px;
    background: #f2f2f2;

    .sub-header {
        margin-bottom: 40px;
        background-image: url(../images/bg_intro.jpg);
    }

    .intro-wrap {
        // position: relative;
        // padding-left: 200px;

        @include clear;
    }

    .sub-contents {
        float: left;
        width: calc(100% - 200px);
    }

    // hijump 소개
    .hijump-intro {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .hijump-intro-conts {
            padding: 23px 30px 63px;
            border-top: 2px solid #606b7f;
            border-bottom: 1px solid #d4d4d4;
            background-color: #fff;

            > p {
                color: #333;
                line-height: 1.8;

                @include fontSize(16px);

                & + p {
                    margin-top: 26px;
                }
            }
        }

        .hijump-intro-flow {
            margin: 33px 0 22px;

            h4.title {
                margin-bottom: 20px;
                color: #dd4f33;
                font-weight: 500;

                @include fontSize(20px);
            }

            .hijump-intro-box {
                padding: 30px;
                background-color: #fbfbfb;
                background-image: url(../images/comm/dot_intro_vertical.gif), url(../images/comm/dot_intro_vertical.gif);
                background-position: 0 0, 100% 0;
                background-repeat: repeat-y, repeat-y;

                @include flexbox;
                @include align-items(center);
                @include justify-content(space-between);
            }

            .subject {
                position: relative;
                padding: 16px 30px;
                color: #fff;
                font-weight: 500;

                @include fontSize(18px);
                @include border-radius(0 15px 0 0);

                &:before {
                    content: "";
                    position: absolute;
                    top: 16px;
                    left: 10px;
                    width: 0;
                    height: 0;
                    border-top: 9px solid transparent;
                    border-left: 14px solid transparent;
                    border-bottom: 9px solid transparent;
                }
            }

            .hijump-intro-service-conts {
                li {
                    margin-top: 14px;
                    color: #666;

                    @include fontSize(16px);

                    em {
                        color: #333;
                        font-weight: 500;
                    }

                    &:first-child {
                        margin-top: 0;
                    }
                }                
            }

            .hijump-intro-service-item {
                width: 370px;

                @include flexbox;

                li {
                    height: 80px;
                    margin-right: 10px;
                    padding: 14px;
                    border: 1px solid #ddd;
                    color: #333;;
                    background-color: #eee;
                    text-align: center;
                    line-height: 1.2;
                    white-space: nowrap;

                    @include flex;
                    @include flexbox;
                    @include align-items(center);
                    @include justify-content(center);
                    @include fontSize(16px);
                    @include border-radius(5px);
                    @include box-sizing;

                    &:last-child {
                        margin-right: 0;
                    }
                }
            }

            .hijump-intro-service {
                border-bottom: 4px solid #80bf1e;
                
                .subject {
                    background-color: #80bf1e;

                    &:before {
                        border-left-color: #67a704;
                    }
                }
            }

            .hijump-intro-data {
                margin-top: 20px;
                border-bottom: 4px solid #2c97d9;
                
                .subject {
                    background-color: #2c97d9;

                    &:before {
                        border-left-color: #1681c3;
                    }
                }
            }
        }
    }

    // 공지사항
    .notice-wrap {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .search-board {
            margin-bottom: 30px;
        }

        .notice-button {
            margin-top: 10px;
            text-align: right;
    
            .btn-medium {
                min-width: 90px;
            }
        }
    
        .paging {
            margin-top: 10px;
        }

        .board-input {
            border-top: 2px solid #606b7f;

            th {
                text-align: center;
            }

            .textarea {
                display: block;
                width: 100%;
                height: 420px;
                margin: 10px 0;
                padding: 20px;
                border: 1px solid #e2e2e2;
                background-color: #fff;

                @include box-sizing;
                @include fontSize(16px);
            }
        }
    }

    // 문의하기
    .qna-wrap {
        padding-top: 15px;

        h3.title {
            margin-bottom: 20px;
            color: #111;
            font-weight: 500;

            @include fontSize(24px);
        }

        .board-input {
            border-top: 2px solid #606b7f;

            th {
                text-align: center;
            }

            .textarea {
                display: block;
                width: 100%;
                height: 420px;
                margin: 10px 0;
                padding: 20px;
                border: 1px solid #e2e2e2;
                background-color: #fff;

                @include box-sizing;
                @include fontSize(16px);
            }
        }

        .qna-button {
            margin-top: 10px;
            text-align: center;
        }
    }

    // 문의
    .help-guide {
        margin-top: 190px;
    }
}